<template>
    <div class="settings">
        <div class="system-header">
            <el-button-group class="btns">
                <el-button class="btn" size="small"  v-for="item in theadName" :key="item.type"
                           v-bind:class="[{mainBgColor:cur===item.type}]" v-on:click="btnGroupFunc(item.type)">{{ item.text }}</el-button>
            </el-button-group>
        </div>
        <router-view></router-view>
    </div>

</template>

<script>
    export default {
        name: "settings",
        data () {
            return {
                cur:0,
                theadName:[
                    {
                        text:this.$L('店铺设置'),
                        type:0,
                        router: 'distributorSettings'
                    },
                    {
                        text:this.$L('推广海报'),
                        type:1,
                        router: 'promotionSettings'
                    }
                ],
            }
        },
        created () {
          if(this.$route.name == 'promotionSettings'){
              this.cur = 1;
          } else {
              this.cur = 0;
          }
        },
        methods : {
            btnGroupFunc(val){
                const _self = this;
                _self.cur = val;
                if(_self.cur === 0){
                    _self.$router.push({name:'distributorSettings'})
                } else if(_self.cur === 1){
                    _self.$router.push({name:'promotionSettings'})
                }
            },

        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common.scss";
    .settings{
        position: relative;
        padding: 0 20px!important;
        margin-top: 10px;
        .system-header{
            height: 42px;
            .btn{
                width: 100px;
            }
            .mainBgColor{
                color: #fff;
            }

        }
    }
</style>